import React from 'react';
import ReactFullpage from '@fullpage/react-fullpage';
import Layout from "../components/layout"
import SEO from "../components/seo"
import SectionWelcome from "../components/sectionWelcome"
import { Container, Row, Col } from "react-bootstrap"

const SEL = 'home-section';
const SECTION_SEL = `.${SEL}`;

// NOTE: if using fullpage extensions/plugins put them here and pass it as props.
// This is no longer required for the scrollOverflow option.
const pluginWrapper = () => {
  /*
  * require('./fullpage.fadingEffect.min'); // Optional. Required when using the "fadingEffect" extension.
  */
};

//const originalColors = ['#ff5f45', '#0798ec', '#fc6c7c', '#435b71', 'orange', 'blue', 'purple', 'yellow'];


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      //sectionsColor: [...originalColors],
      fullpages: [
        {
          title: 'Welcome',
          slug: 'welcome',
          content: <SectionWelcome/>,
        },
        {
          title: 'Expertise',
          slug: 'expertise',
          content: "",
        },
        {
          title: 'Selected Projects',
          slug: 'selected-projects',
          content: "",
        },
        {
          title: 'Client List',
          slug: 'client-list',
          content: "",
        },
        {
          title: 'Contact',
          slug: 'contact',
          content: "",
        }
      ],
    };
  }

  onLeave(origin, destination, direction) {
    console.log('onLeave', { origin, destination, direction });
    // arguments are mapped in order of fullpage.js callback arguments do something
    // with the event
  }


  moveSectionDown() {
    window.fullpage_api.moveSectionDown();
  }

  render() {
    const { fullpages } = this.state;

    if (!fullpages.length) {
      return null;
    }    
    console.log('fullpages = '+fullpages);

    return (
      <Layout pageInfo={{ pageName: "index" }}>
      <SEO title="Home" keywords={[`gatsby`, `react`, `bootstrap`]} />
      <div className="App">
  
        <ReactFullpage
          debug /* Debug logging */

          // Required when using extensions
          pluginWrapper={pluginWrapper}

          // fullpage options
          licenseKey={'75AEE7FB-1CA04072-A388ED7C-844E24BC'} // Get one from https://alvarotrigo.com/fullPage/pricing/
          navigation
          anchors={['welcome', 'expertise', 'selected-projects', 'client-list', 'contact']}
          sectionSelector={SECTION_SEL}
          onLeave={this.onLeave.bind(this)}
          sectionsColor={this.state.sectionsColor}

          render={comp => (
            <ReactFullpage.Wrapper>
              {fullpages.map(({ title, slug, content }) => (
                <div key={slug} className={SEL}>
                  <h2 className="section-title">{title}</h2>
                  {content}
                </div>
              ))}
            </ReactFullpage.Wrapper>
          )}
        />
      </div>
      </Layout>
    );
  }
}

export default App;